const mistakes = {
    0: "pass",
    1: "too many symbols",
    2: "not enough symbols",
    3: "regular not passed",
    4: "number too big",
    5: "number too small",

}


export const checkStringInput = (str, max, min, re = /./) => {
    if (str.length > max){
        return mistakes[1]
    }
    if (str.length < min){
        return mistakes[2]
    }
    if (re.test(String(str).toLowerCase())){
        return mistakes[3]
    }
    return mistakes[0]
}

export const checkIntInput = (num, maxNum, minNum) => {
    if (num > maxNum){
        return mistakes[4]
    }
    if (num < minNum){
        return mistakes[5]
    }
    return mistakes[0]
}


export const getGradeValues = (grade) => {
    switch(grade) {
        case 'internship':  
            return ''
        case 'junior':  
            return ' '
        case 'middle':  
            return ''
        case 'senior':  
            return ' '
        case 'director':  
            return ' '
        case 'senior-director':  
            return ' '
      
        default:
            break
    }
}

export const getEducationValues = (education) => {
    switch(education) {
        case 'course':  
            return ''
        case 'primary':  
            return '  (4 )'
        case 'basic':  
            return '   (9 )'
        case 'secondary':  
            return '   (11 )'
        case 'post-secondary':  
            return '  '
        case 'bachelor':  
            return ' ()'
        case 'specialist':  
            return ' ()'
        case 'master':  
            return ' ()'
        case 'PhD-asp':  
            return ''
        case 'PhD-doc':  
            return 'a'
      
        default:
            break
      }
}

