export const getLanguageValues = (language) => {
    switch(language) {
        case 'A1':  
            return 'A1 -  (Beginner)'
        case 'A2':  
            return 'A2 -  (Pre-Intermediate)'
        case 'B1':  
            return 'B1 -  (Intermediate)'
        case 'B2':  
            return 'B2 -  (Upper-Intermediate)'
        case 'C1':  
            return 'C1 -  (Advanced)'
        case 'C2':  
            return 'C2 -    (Mastery)'
        
        default:
            break
    }
}